@mixin responsive-font($pc-size, $mobile-size) {
  $add-size: $pc-size - $mobile-size;
  $add-mobile-size: $add-size + $add-size * 0.7;
  @media (max-width: 767px) {
      font-size: calc(#{$mobile-size + px} + #{$add-mobile-size} * ((100vw - 320px) / #{$max-width}));
  }
  @media (min-width: 767px) {
      font-size: calc(#{$mobile-size + px} + #{$add-size} * (100vw / #{$max-width}));
  }
}